.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.nav-links li {
  margin: 1rem;
}

.nav-links a {
  border: 1px solid transparent;
  color: #292929;
  text-decoration: none;
  padding: 0.5rem;
}

.nav-links a:hover,
.nav-links a:active,
.nav-links a.active {
  background: orange;
  border-color: #292929;
  color: white;
}

.nav-links button {
  cursor: pointer;
  border: 1px solid #292929;
  color: #292929;
  background: transparent;
  font: inherit;
  padding: 0.5rem;
}

.nav-links button:focus {
  outline: none;
}

.nav-links button:hover,
.nav-links button:active {
  background: #292929;
  color: white;
}

@media (min-width: 768px) {
  .nav-links {
    flex-direction: row;
  }

  .nav-links li {
    margin: 0 0.5rem;
  }

  .nav-links a {
    color: black;
    text-decoration: none;
  }

  .nav-links button {
    border: 1px solid black;
    color: black;
    background: transparent;
  }

  .nav-links button:hover,
  .nav-links button:active {
    background: #f8df00;
    color: #292929;
  }
}





.nav-links li {
  display: flex; 
  align-items: center;
  justify-content: center;
  margin: 1rem 0.5rem; }

.nav-links a, 
.nav-links button {
  display: inline-flex; 
  align-items: center; 
  justify-content: center;
  text-align: center; 
  padding: 0.5rem; 
  margin: 0;
}

.nav-links button {
  border: 1px solid #292929;
  background: transparent;
  font: inherit;
}